.main {
  position: relative;
  z-index: 2;
  &:before {
    content: ' ';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    opacity: 0.15;
    background-image: url("./chainlink.png");
  }
}

body {
  background: black;
  margin: 0;
}


